import { useState } from 'react';
import { Button, TextField } from '@mui/material';
import style from './Login.module.scss'
import axios from 'axios';

export const Login = () => {

    const dt = new Date().toLocaleString()

    function loginRealizado(a) {
        if (a === "try") {
            document.getElementById('login--modal').showModal();
            document.getElementById('msg').innerHTML = "Verificando usurio e senha!"
        } else if (a === "successo") {
            setTimeout(() => {
                document.getElementById('msg').innerHTML = `Login realizado com sucesso! ${dt}`
            }, 3000)
        } else if (a === "incorreto") {
            setTimeout(() => {
                document.getElementById('msg').innerHTML = "Usurio ou senha incorreto."
            }, 3000)
        } else if (a === "erro") {
            setTimeout(() => {
                document.getElementById('msg').innerHTML = "Houve um erro ao tentar logar no servidor, tente novamente em alguns minutos."
            }, 3000)
        } else if (a === 'inativo') {
            setTimeout(() => {
                document.getElementById('msg').innerHTML = "Usurio inativo ou bloqueado. Favor entrar em contato com o administrador do sistema."
            }, 3000)
        }
    }
    function fechaDiag() {
        let b = document.getElementById('msg').innerHTML
        if (b !== "Verificando usurio e senha!") {
            document.getElementById('login--modal').close();
        }
    }

    const LoginRealiza = () => {
        return (
            <>
                <dialog id='login--modal' onClick={fechaDiag}>
                    <div>
                        <p id='msg'></p>
                    </div>
                </dialog>
            </>
        )
    }

    const [login, setLogin] = useState();
    const [senha, setSenha] = useState();

    const Login = (e) => {
        e.preventDefault()
        loginRealizado("try")
        axios.post('http://104.248.40.57:3001/Login', {
            login: login,
            senha: senha
        })
            .then((response) => {
                if (response.status === 200) {
                    if ((!response.data) || (response.data === '')) {
                        loginRealizado("incorreto")
                    } else {
                        // console.log(response.data)
                        localStorage.setItem('user', JSON.stringify(response.data))
                    }
                }
            })
            .then(() => {
                let usuario = JSON.parse(localStorage.getItem('user'))
                axios.get('http://104.248.40.57:3001/UserLoading', {
                    params: { usuario }
                })
                    .then(res => {
                        // console.log(res.data[0].situacao)
                        // if (res.data[0].situacao === 'ativo') {
                            loginRealizado("successo")
                            localStorage.setItem('usuario', JSON.stringify(res.data[0]))
                            setTimeout(() => {
                                window.location.href = "Dashboard";
                            }, 5000)
                        // } else {
                        //     loginRealizado("inativo")
                        // }
                    })
                    .catch(err => console.log(err))
            })
            .catch((err) => {
                console.log(err);
                loginRealizado("erro")
            })
            .finally(() => {

                document.getElementById('login--form').reset()
            })
    }
    return (
        <>
            <div className={style.login}>
                <h1>Login</h1>
                <form id='login--form' className={style['login--form']} onSubmit={Login}>
                    <div className={style['login-user']}>
                        <TextField
                            id='login'
                            label='Usurio'
                            onChange={(e) => setLogin(e.target.value)}
                            color='success'
                            size='small'
                            fullWidth required />
                    </div>
                    <div className={style['login-user']}>
                        <TextField
                            id='senha'
                            label='Senha'
                            type='password'
                            onChange={(e) => setSenha(e.target.value)}
                            color='success'

                            size='small'
                            fullWidth required />
                    </div>
                    <div className={style['login-user']}>
                        <Button
                            variant='contained'
                            type='onSubmit'
                            color='success'
                            fullWidth>
                            Logar
                        </Button>
                    </div>
                    <p>Esqueci minha senha</p>
                </form>
                <LoginRealiza />
            </div>
            <div className={style['box--container']}>
                <h1>Confira os servios disponveis:</h1>
                <div>
                    <div>
                        <h2>Alteraes de Documento</h2>
                        <li>Alterao de Informaes do Proprietrio</li>
                        <li>Alterao de Informaes de Veculos</li>
                        <li>Alterao de Restrio Financeira</li>
                    </div>
                    <div>
                        <h2>Autorizao</h2>
                        <li>Autorizao para Alterao de Caracteristicas</li>
                        <li>Autorizao para Fabricao de Placa</li>
                        <li>Autorizao para Regravao de Chassi</li>
                        <li>Autorizao para Regravao de Motor</li>
                        <li>Autorizao para Colocao de Lacre em Placa</li>
                    </div>
                    <div>
                        <h2>Baixa</h2>
                        <li>Baixa de Placa de Experincia ou de Fabricante</li>
                        <li>Baixa para Militarizao</li>
                        <li>Baixa para outro UF</li>
                        <li>Baixa para outro Pais</li>
                        <li>Baixa Simples de Veculo</li>
                    </div>
                    <div>
                        <h2>Processo</h2>
                        <li>Comunicao de Processo</li>
                        <li>Cancelamento de Processo</li>
                    </div>
                    <div>
                        <h2>Correo</h2>
                        <li>Correo das Observaes do CRLV-E</li>
                        <li>Correo de Chassi</li>
                        <li>Correo de Informaes do Proprietrio</li>
                        <li>Correo de Informaes do Veculo</li>
                        <li>Correo de Municpio</li>
                        <li>Correo de Restries</li>
                    </div>
                    <div>
                        <h2>Incluso</h2>
                        <li>Incluso de Restrio Financeira</li>
                        <li>Incluso de Placa de Experincias ou de Fabricante</li>
                        <li>Incluso de Averbao de Execuo</li>
                    </div>
                    <div>
                        <h2>Troca/Transferncia</h2>
                        <li>Transferncia de Propriedade de Veculo de Outra UF</li>
                        <li>Transferncia de Propriedade de Veculo do RS</li>
                        <li>Troca de Municpio de Veculo de Outra UF</li>
                        <li>Troca de Municpio de Veculo do RS</li>
                    </div>
                    <div>
                        <h2>Outros</h2>
                        <li>Emisso de Certido</li>
                        <li>Impresso do CRLV-E</li>
                        <li>Liberao de Restrio Financeira</li>
                        <li>Licena Especial de Trnsito</li>
                        <li>Mudana para Placa nica de Placa Mercosul</li>
                        <li>Primeiro Emplacamento</li>
                        <li>Renovao de Placa de Experincia ou Fabricante</li>
                        <li>Reserva de Placa</li>
                        <li>Restrio de Transferncia</li>
                        <li>Solicitao de Vistoria</li>
                    </div>
                </div>
            </div>
        </>
    )
}
